@import url(https://fonts.googleapis.com/css?family=Roboto:400,900);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.link {
  cursor:pointer;
}


.countdown {
  position:relative;
  width:100%;
}
.countdown div {
  position: absolute;
  width:100%;
  text-align:center;
  font-size: 140px;
  color: #778F9B;
  text-align: center;
  opacity: 0;
}
.countdown div.num0 {
  font-size: 20px;
  color: #000;
  opacity:0;
}

@keyframes time {
  0%, 10%, 100% {
    opacity: 0;
    transform: scale(1);
  }
  5% {
    opacity: 0.8;
    transform: scale(1.2);
  }
}
@keyframes end {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  5%, 100% {
    opacity: 0.8;
    transform: scale(1.2);
  }
}
.countdown .num3, .countdown .num2, .countdown .num1 {
  color: #FF1643;
}

.countdown .num0 {
  animation: end 10s 1;
  /*animation: 1s linear 1;*/
  animation-delay: 5s;
  animation-fill-mode:forwards;
}

.countdown .num1 {
  animation: time 10s 1;
  /*animation: 1s linear 1;*/
  animation-delay: 4s;
}

.countdown .num2 {
  animation: time 10s 1;
  /*animation: 1s linear 1;*/
  animation-delay: 3s;
}

.countdown .num3 {
  animation: time 10s 1;
  /*animation: 1s linear 1;*/
  animation-delay: 2s;
}

.countdown .num4 {
  animation: time 10s 1;
  /*animation: 1s linear 1;*/
  animation-delay: 1s;
}

.countdown .num5 {
  animation: time 10s 1;
  /*animation: 1s linear 1;*/
  animation-delay: 0s;
}

.error {
  border:1px solid red;
}
.error-msg {
  color:red;
}
